<template>
  <div>
    <div class="row row--15">
      <div class="col-lg-12">
        <ul
          id="pills-tab"
          class="nav nav-pills rwt-portfolio-filter filter-button-default liststyle mb--20"
          role="tablist"
          data-aos="fade-up"
        >
          <li
            v-for="(filter, index) in filters"
            :key="`tab-button-${filter.id}`"
            class="nav-item list-item"
            role="presentation"
          >
            <button
              :id="`pills-${filter.text}-tab`"
              class="nav-link"
              :class="{'active': index === 0}"
              data-bs-toggle="pill"
              :data-bs-target="`#pills-${filter.text}`"
              type="button"
              role="tab"
              :aria-controls="`pills-${filter.text}`"
              :aria-selected="index === activeTab ? 'true' : 'false'"
              @click="activeTab = index"
            >
              {{ filter.text }}
            </button>
          </li>
        </ul>
      </div>
    </div>
    <div
      id="pills-tabContent"
      class="tab-content"
    >
      <div
        v-for="(filter, index) in filters"
        :id="`pills-${filter.text}`"
        :key="`tab-pane-${filter.id}`"
        class="tab-pane fade"
        :class="{'show active': index === 0}"
        role="tabpanel"
        :aria-labelledby="`pills-${filter.text}-tab`"
      >
        <div class="row row--15">
          <div v-if="filter.text === 'all'">
            <template v-for="portfolio in portfolioData" :key="portfolio.id">
              <div
                :class="`${column} portfolio`"
                data-aos="fade-up"
              >
                <div class="rwt-card">
                  <div class="inner">
                    <div class="thumbnail">
                      <figure class="card-image">
                        <router-link :to="`/portfolio-details/${portfolio.id}`">
                          <img
                            class="img-fluid"
                            :src="portfolio.portfolioImage"
                            alt="Portfolio-01"
                          >
                        </router-link>
                      </figure>
                      <router-link
                        :to="`/portfolio-details/${portfolio.id}`"
                        class="rwt-overlay"
                      />
                    </div>
                    <div class="content">
                      <h5 class="title mb--10">
                        <router-link :to="`/portfolio-details/${portfolio.id}`">
                          {{ portfolio.title }}
                        </router-link>
                      </h5>
                      <span class="subtitle b2 text-capitalize">{{ portfolio.category }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div v-if="filter.text !== 'all'">
            <template v-for="portfolio in portfolioData" :key="portfolio.id">
              <div v-if="filter.text === portfolio.category" :class="`${column} portfolio`">
                <div class="rwt-card">
                  <div class="inner">
                    <div class="thumbnail">
                      <figure class="card-image">
                        <router-link :to="`/portfolio-details/${portfolio.id}`">
                          <img
                            class="img-fluid"
                            :src="portfolio.portfolioImage"
                            alt="Portfolio-01"
                          >
                        </router-link>
                      </figure>
                      <router-link
                        :to="`/portfolio-details/${portfolio.id}`"
                        class="rwt-overlay"
                      />
                    </div>
                    <div class="content">
                      <h5 class="title mb--10">
                        <router-link :to="`/portfolio-details/${portfolio.id}`">
                          {{ portfolio.title }}
                        </router-link>
                      </h5>
                      <span class="subtitle b2 text-capitalize">{{ portfolio.category }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
    <div class="row row--15">
      <div class="col-lg-12 text-center">
        <div
          class="rwt-load-more text-center mt--60"
          data-aos="fade-up"
        >
          <a
            :class="buttonClass"
            href="#"
          >
            Load More <Icon
              name="loader"
              icon-class="icon"
            />
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import Icon from '../../icon/Icon'

    export default {
        name: 'Portfolio',
        components: {Icon},
        props: {
            portfolioData: {},
            buttonClass: {
                type: String,
                default: 'btn btn-default btn-large btn-icon'
            },
            column: {
                type: String,
                default: 'col-lg-6 col-md-6 col-sm-12 col-12 mt--30'
            }
        },
        data() {
            return {
                filters: [
                    {
                        id: 1,
                        text: 'all',
                    },
                    {
                        id: 2,
                        text: 'design',
                    },
                    {
                        id: 3,
                        text: 'art',
                    },
                    {
                        id: 4,
                        text: 'development',
                    }
                ],
                activeFilter: '',
                activeTab: 0
            }
        }
    }
</script>
