<template>
  <ul>
    <template v-for="(post, index) in posts" :key="index">
      <li v-if="index < 4">
        <router-link
          class="d-block"
          :to="'#'"
        >
          {{ post.title }}
        </router-link>
        <span class="cate">{{ post.categories[0] }}</span>
      </li>
    </template>
  </ul>
</template>

<script>
    import BlogPostMixin from '../../mixins/BlogPostMixin'

    export default {
        name: 'BlogSidebarPost',
        mixins: [BlogPostMixin]
    }
</script>
